      getCenterNoticeFunc() {
        var that = this;
        var param = {
          "location": ''
        }
        $.ajax({
          type: 'post',
          url: getService() + "/announcement/findCommonAnnouncement",
          dataType: "json",
          data: JSON.stringify(param),
          async: true,
          headers: {'Content-Type': 'application/json'},
          xhrFields: {
            withCredentials: true
          },
          crossDomain: true,
          success: function (res) {
            console.log('res=', res);
            if (res.error_code == 0) {
              that.getCenterNotice.showFlag = true;
              that.getCenterNotice.noticeArray = res.data;
            }

          },
          error: function (res) {

          }
        });

      },
      getInfo: function () {
        $.ajax({
          type: 'get',
          url: getService() + "/user/getInfo",
          dataType: "json",
          data: {},
          xhrFields: {
            withCredentials: true
          },
          crossDomain: true,
          success: function (res) {
            if (res.error_code == 0) {
              this.userInfo = res.data;
              this.secretPhone = this.setSecretPhone(this.userInfo.tel_number);
              this.bankNum = this.userInfo.bankAccounts.length;
              this.zhifubaoNum = this.userInfo.aliPayAccounts.length;
              this.wechatNum = this.userInfo.wechatAccounts.length;
              this.lockPhone = this.userInfo.tel_number.substr(0, 2) + '****' + this.userInfo.tel_number.substr(6, 4);
            }
          }.bind(this)
        });
      },
      getTypes: function () {
        $.ajax({
          type: 'get',
          url: getService() + "/dujiuxing",
          dataType: "json",
          data: {},
          xhrFields: {
            withCredentials: true
          },
          crossDomain: true,
          success: function (res) {
            if (res.error_code == 0) {
              this.typeList = res.data;
              this.typeOptions = this.typeList.bank;
            }
          }.bind(this)
        });
      },
      showAppealModal: function () {
        $('#appealModal').modal('show');
      },
      showBankModal: function () {
        $('#bankModal').modal('show');
      },
      setSecretPhone: function (num) {
        var starPhone = num.substr(0, 3) + '***' + num.substr(7);
        return starPhone;
      },
      submitAccount: function () {
        var typeStr = '';
        switch (this.type) {
          case 'bank':
            typeStr = '';
            break;
          case 'postOffice':
            typeStr = '';
            break;
          case 'cooperative':
            typeStr = '';
            break;
          case 'association':
            typeStr = '';
            break;
          case 'fishing':
            typeStr = '';
            break;
          default:
            break;
        }
        var param = {
          bankType: typeStr,
          bankName: this.bankName,
          accountName: this.accountName,
          last6digital: this.sixCode,
          accountTelNumber: this.userInfo.tel_number
        }
        $.ajax({
          type: 'post',
          url: getService() + "/addbank",
          dataType: "json",
          data: JSON.stringify(param),
          async: true,
          headers: {'Content-Type': 'application/json'},
          xhrFields: {
            withCredentials: true
          },
          crossDomain: true,
          success: function (res) {
            console.log(res);
          }.bind(this)
        });
      },
      showPersonalModal: function () {
        $('#personalModal').modal('show');
      },
      submitInfo: function () {
        var _this = this;
        switch (this.personalType) {
          case 'referee':
            if (this.referer != '') {
              var param = {
                referer: this.referer
              }
              $.ajax({
                type: 'post',
                url: getService() + "/setReferer",
                dataType: "json",
                data: JSON.stringify(param),
                async: true,
                headers: {'Content-Type': 'application/json'},
                xhrFields: {
                  withCredentials: true
                },
                crossDomain: true,
                success: function (res) {
                  if (res.error_code == 0) {
                    $('#personalModal').modal('hide');
                    alert('');
                  } else {
                    alert(res.error_massage);
                  }
                }.bind(this)
              });
            } else {
              alert('ID');
            }
            break;
          case 'validate':
            if (this.realIDNumber != '' && this.realName != '') {
              var reg = /^[a-zA-Z][0-9]{9}$/;
              if (reg.test(this.realIDNumber)) {
                var param = {
                  realIDNumber: this.realIDNumber,
                  realName: this.realName
                }
                $.ajax({
                  type: 'post',
                  url: getService() + "/addUserRealName",
                  dataType: "json",
                  data: JSON.stringify(param),
                  async: true,
                  headers: {'Content-Type': 'application/json'},
                  xhrFields: {
                    withCredentials: true
                  },
                  crossDomain: true,
                  success: function (res) {
                    if (res.error_code == 200) {
                      alert('')
                    } else {
                      alert(res.error_massage);
                    }
                  }.bind(this)
                });
              } else {
                alert('');
              }
            } else {
              alert('');
            }
            break;
          case 'phone':
            $.ajax({
              type: 'post',
              url: getService() + "/user/updatePhoneNumber",
              dataType: "json",
              data: {
                tel_number: this.newPhone,
                code: this.newPhoneCode
              },
              xhrFields: {
                withCredentials: true
              },
              crossDomain: true,
              success: function (res) {
                if (res.error_code == 0) {
                  alert('');
                  $('#personalModal').modal('hide');
                  _this.$router.push({path: '/login'});
                } else {
                  alert(res.error_msg);
                }
              }
            });
            break;
          case 'password':
            if (this.newPassword != this.newPasswordCheck) {
              alert('');
            } else {
              $.ajax({
                type: 'post',
                url: getService() + "/user/updatePassword",
                dataType: "json",
                data: {
                  currentPassword: this.currentPassword,
                  newPassword: this.newPassword
                },
                xhrFields: {
                  withCredentials: true
                },
                crossDomain: true,
                success: function (res) {
                  if (res.error_code == 200) {
                    alert('');
                    $('#personalModal').modal('hide');
                    _this.$router.push({path: '/login'});
                  } else {
                    alert(res.error_massage);
                  }
                }
              });
            }
            break;
          case 'email':
            if (this.newEmail == '') {
              alert('');
            } else {
              $.ajax({
                type: 'post',
                url: getService() + "/mail/change_Email",
                dataType: "json",
                data: {
                  code: this.emailCode,
                  email_address: this.newEmail
                },
                xhrFields: {
                  withCredentials: true
                },
                crossDomain: true,
                success: function (res) {
                  if (res.error_code == 0) {
                    alert('');
                    $('#personalModal').modal('hide');
                  } else {
                    alert(res.error_msg);
                  }
                }
              });
            }
            break;
          case 'nickName':
            if (this.nickName == '') {
              alert('');
            } else {
              $.ajax({
                type: 'post',
                url: getService() + "/user/updateNickname",
                dataType: "json",
                data: {
                  nickName: this.nickName
                },
                xhrFields: {
                  withCredentials: true
                },
                crossDomain: true,
                success: function (res) {
                  if (res.error_code == 200) {
                    alert('');
                    $('#personalModal').modal('hide');
                  } else {
                    alert(res.error_msg);
                  }
                }
              });
            }
            break;
          default:
            break;
        }
      },
      setPersonalType: function (str) {
        this.personalType = str;
        switch (str) {
          case 'referee':
            $('#validateRow').hide();
            $('#phoneRow').hide();
            $('#documentRow').hide();
            $('#emailRow').hide();
            $('#passwordRow').hide();
            $('#nickNameRow').hide();
            $('#refereeRow').show();
            $('#nextStepBtn').hide();
            $('#saveAndSendBtn').show();
            break;
          case 'validate':
            $('#refereeRow').hide();
            $('#phoneRow').hide();
            $('#documentRow').hide();
            $('#emailRow').hide();
            $('#passwordRow').hide();
            $('#nickNameRow').hide();
            $('#validateRow').show();
            $('#nextStepBtn').hide();
            $('#saveAndSendBtn').show();
            break;
          case 'phone':
            $('#refereeRow').hide();
            $('#validateRow').hide();
            $('#documentRow').hide();
            $('#emailRow').hide();
            $('#passwordRow').hide();
            $('#nickNameRow').hide();
            $('#phoneRow').show();
            $('#saveAndSendBtn').hide();
            $('#nextStepBtn').show();
            break;
          case 'document':
            $('#refereeRow').hide();
            $('#validateRow').hide();
            $('#phoneRow').hide();
            $('#emailRow').hide();
            $('#passwordRow').hide();
            $('#nickNameRow').hide();
            $('#documentRow').show();
            $('#saveAndSendBtn').hide();
            $('#nextStepBtn').hide();
            break;
          case 'email':
            $('#refereeRow').hide();
            $('#validateRow').hide();
            $('#phoneRow').hide();
            $('#documentRow').hide();
            $('#passwordRow').hide();
            $('#nickNameRow').hide();
            $('#emailRow').show();
            $('#nextStepBtn').hide();
            $('#saveAndSendBtn').show();
            break;
          case 'password':
            $('#refereeRow').hide();
            $('#validateRow').hide();
            $('#phoneRow').hide();
            $('#documentRow').hide();
            $('#emailRow').hide();
            $('#nickNameRow').hide();
            $('#passwordRow').show();
            $('#nextStepBtn').hide();
            $('#saveAndSendBtn').show();
            break;
          case 'nickName':
            $('#refereeRow').hide();
            $('#validateRow').hide();
            $('#phoneRow').hide();
            $('#documentRow').hide();
            $('#emailRow').hide();
            $('#passwordRow').hide();
            $('#nickNameRow').show();
            $('#nextStepBtn').hide();
            $('#saveAndSendBtn').show();
            break;
          default:
            break;
        }
      },
      sendCode: function () {
//	  		if (this.registPhone == '') {
//	  			$('#message').html('');
//	  			$('#myModal').modal('show');
//	  		}
//	  		else if(!this.registPhoneConfig.test(this.registPhone)){
//	  			$('#message').html('');
//	  			$('#myModal').modal('show');
//	  		}
//	  		else{
        $('#sendCodeBtn').attr('disabled', 'disabled');
        $('#sendCodeBtn').css('background', '#ccc');
        this.timeInterval = setInterval(this.setTimer, 1000);
        $.ajax({
          type: 'get',
          url: getService() + "/msg/currentSendMassage",
          dataType: "json",
//		  			data:{
//		  				tel_number:this.userInfo.tel_number
//		  			},
          xhrFields: {
            withCredentials: true
          },
          crossDomain: true,
          success: function (res) {
            console.log(res);
          }
        });
//	  		}

      },
      setTimer: function () {
        $('#sendCodeBtn').html(this.timer + '');
        this.timer = parseInt(this.timer) - 1;
        if (this.timer == -1) {
          clearInterval(this.timeInterval);
          this.timer = 60;
          $('#sendCodeBtn').removeAttr('disabled');
          $('#sendCodeBtn').css('background', '#00a6ff');
          $('#sendCodeBtn').html('');
        }
      },
      setNewTimer: function () {
        $('#sendNewCodeBtn').html(this.timer + '');
        this.timer = parseInt(this.timer) - 1;
        if (this.timer == -1) {
          clearInterval(this.timeNewInterval);
          this.timer = 60;
          $('#sendNewCodeBtn').removeAttr('disabled');
          $('#sendNewCodeBtn').css('background', '#00a6ff');
          $('#sendNewCodeBtn').html('');
        }
      },
      setEmailTimer: function () {
        $('#sendEmailCodeBtn').html(this.timer + '');
        this.timer = parseInt(this.timer) - 1;
        if (this.timer == -1) {
          clearInterval(this.timeEmailInterval);
          this.timer = 60;
          $('#sendEmailCodeBtn').removeAttr('disabled');
          $('#sendEmailCodeBtn').css('background', '#00a6ff');
          $('#sendEmailCodeBtn').html('');
        }
      },
      nextStep: function () {
        if (this.changePhoneCode == '') {
          alert('')
        }
        else {
          $.ajax({
            type: 'post',
            url: getService() + "/user/verifySendMassage",
            dataType: "json",
            data: {
              code: this.changePhoneCode
            },
            xhrFields: {
              withCredentials: true
            },
            crossDomain: true,
            success: function (res) {
              if (res.error_code == 0) {
                $('#phoneSet1').hide();
                $('#phoneSet2').show();
                $('#nextStepBtn').hide();
                $('#saveAndSendBtn').show();
              } else {
                alert(res.error_msg);
              }
            }
          });
        }
      },
      sendNewCode: function () {
        if (this.newPhone == '') {
          alert('');
        }
        else {
          $('#sendNewCodeBtn').attr('disabled', 'disabled');
          $('#sendNewCodeBtn').css('background', '#ccc');
          this.timeNewInterval = setInterval(this.setNewTimer, 1000);
          $.ajax({
            type: 'post',
            url: getService() + "/msg/updatePhoneNumberSendMassage",
            dataType: "json",
            data: {
              tel_number: this.newPhone
            },
            xhrFields: {
              withCredentials: true
            },
            crossDomain: true,
            success: function (res) {
              if (res.error_code == 0) {
                console.log(res);
              } else {
                alert(res.error_msg);
              }
            }
          });
        }
      },
      sendEmailCode: function () {
        if (this.newEmail == '') {
          alert('');
        }
        else {
          $('#sendEmailCodeBtn').attr('disabled', 'disabled');
          $('#sendEmailCodeBtn').css('background', '#ccc');
          this.timeEmailInterval = setInterval(this.setEmailTimer, 1000);
          $.ajax({
            type: 'post',
            url: getService() + "/mail/send_mail",
            dataType: "json",
            data: {
              email_address: this.newEmail
            },
            xhrFields: {
              withCredentials: true
            },
            crossDomain: true,
            success: function (res) {
              if (res.error_code == 0) {
                console.log(res);
              } else {
                alert(res.error_msg);
              }
            }
          });
        }
      },
      addWechatPopup() {
        $.ajax({
          type: 'get',
          url: getService() + "/wechat/getQRcodeUrl",
          dataType: "json",
          data: {},
          xhrFields: {
            withCredentials: true
          },
          crossDomain: true,
          success: function (res) {
            if (res.error_code == 0 || res.error_code == 200) {
              this.wechatPic = res.data.QRUrl;
              $('#popUpWechat').show();
            }
          }.bind(this)
        });
      },
      sendAlipayAccountFunc() {
        let url = getService() + `/alipay/QRcode?alipayAccount=${this.alipayAccountInput}`;
        console.log(url)
        this.alipayImg = url;
      },
      showModalByAlipay() {
        $('#popUpAlipay').modal('show');

      }
    }
  }
</script>

<style>
  @import url("../../assets/css/PCstyle.css");
</style>



